.Header{
    background-color: #c20324;
    color: white;
    text-align: center;
    height: 100px;
    font-size: 2rem;
    font-weight: 600;
    display: flex;
}

.Header p{
    margin-left: 10px;
    margin-top: 25px;
}
div.taxi-wrap {    
    background-color: #DB4E4E;
    width: 790px;    
    margin: 0 auto;
    margin-top: 30px;
}
.taxi-container{
    background-color: white;
    margin-left: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: auto;
    color: black;
    font-size: 15px;
    font-weight: 500;
    display: inline-block;
    width: 250px;
    height: 170px;
}

.taxi-container:hover{
    background-color: #E0E0E0;
}

.taxi-container p{
    text-align: center;
}
.outer-search{
    display: flex;
    margin-bottom: 2%;
}

.inner-search{    
    margin: 0 auto;    
}
body{
    background-color: #F9E076;
}
#detail{
    background-color: #E0E0E0;    
    text-align: center;
    padding: 40px;    
    width: 175px;
}
